.App {
  width:100%;
  height:100%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  background: #c0392b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #8e44ad, #c0392b);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#8e44ad), to(#c0392b));
  background: -webkit-linear-gradient(right, #8e44ad, #c0392b);
  background: -o-linear-gradient(right, #8e44ad, #c0392b);
  background: linear-gradient(to left, #8e44ad, #c0392b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */  
}

.gen-name{
  font-size: 3em;
  font-weight: bold;
  color:#eee;
  padding:2em;
}

.button{
  background:#8e44ad;
  outline: none;
  cursor: pointer;
  color: #fff;
  font-size: 1.2em;
  padding:1em 2em;  
  border: 1px solid #592B6C;
  border-radius: 20em;
  -webkit-box-shadow: 0px 7px 20px -5px #333;
          box-shadow: 0px 7px 20px -5px #333;
  -webkit-transition:0.3s all;
  -o-transition:0.3s all;
  transition:0.3s all;
}


.button:hover{
  background:#AB55CF;
  -webkit-transform: translateY(-4px);
      -ms-transform: translateY(-4px);
          transform: translateY(-4px);
}


.button:active, .button:target{
  background:#8e44ad;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}


